import React from 'react';
import {connect} from 'react-redux';

import {goBack} from '../../store/router/actions';
import {setFormData} from "../../store/formData/actions";

import {
    Div,
    Checkbox,
    Input,
    FormLayout,
    FormLayoutGroup,
    Panel,
    Group,
    Button,
    PanelHeader,
    PanelHeaderBack
} from "@vkontakte/vkui";

import Icon24Bug from '@vkontakte/icons/dist/24/bug';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline';


class HomePanelProfile extends React.Component {

    constructor(props) {
        super(props);

        let defaultInputData = {
            workplace: '',
            workposition: '',

            checkboxPhoto: 0,
            checkboxOnline: 0
        };

        this.state = {
            inputData: props.inputData['example_form'] || defaultInputData
        };

        this.handleInput = (e) => {
            let value = e.currentTarget.value;

            if (e.currentTarget.type === 'checkbox') {
                value = e.currentTarget.checked;
            }

            this.setState({
                inputData: {
                    ...this.state.inputData,
                    [e.currentTarget.name]: value
                }
            })
        };

        this.clearForm = () => {
            this.setState({
                inputData: defaultInputData
            });
        };
    }

    componentWillUnmount() {
        this.props.setFormData('example_form', this.state.inputData);
    }

	test = () => {
		fetch(`test`)
	}

    render() {
        const {id, goBack} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader
                    left={<PanelHeaderBack onClick={() => goBack()}/>}
                >
                    Добавление
                </PanelHeader>
<Group title="Администраторская">
<Div>
<Button 
                        before={<Icon24Bug/>}
                        size="xl" 
                        level="secondary" 
                        component="a" 
                        target="_blank"> Включить акцию X2 </Button> 
</Div>
<Div>
<Button 
                        before={<Icon24List/>}
                        size="xl" 
                        level="secondary" 
                        component="a" 
                        target="_blank">  Просмотр статистики пользователей </Button> 
</Div>
<Div>

<Button 
                        before={<Icon28BugOutline/>}
                        size="xl" 
                        level="secondary" 
                        component="a" 
                        target="_blank">Забанить пользователя в сервисе</Button> 
</Div>
</Group>






            </Panel>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        inputData: state.formData.forms,
    };
};

const mapDispatchToProps = {
    setFormData,
    goBack
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);